<template>
    <div id="app">
        <div class="fixed">
            <img alt="logo" src="./assets/6263.jpg" />
            <SearchBar @searching="searching = $event" />
        </div>
        <ul>
            <li @click="selectBook(book)" v-for="book in sortedBooks" :key="book.id" :class="{active: book.id == selectedBook.id}">
                <i @click="removeBook" v-if="book.id == selectedBook.id" class="material-icons trash">delete</i>
                <div class="checkbox">
                    <i @click="switchReaded" v-if="book.id == selectedBook.id && !book.readed" class="material-icons trash">check_box_outline_blank</i>
                    <i @click="switchReaded" v-if="book.id == selectedBook.id && book.readed" class="material-icons trash">check_box</i>
                </div>
                <i @click="editBook()" v-if="book.id == selectedBook.id" class="material-icons edit">edit</i>
                {{ book.author }} - {{ book.title }}
                <i v-if="book.readed" class="material-icons check">check</i>
            </li>
        </ul>
        <AddButton @clicked="modalAddBook" />
        <MyModal v-if="modal.show" :modalType="modal.type" :activeBook="selectedBook" @closeModal="modal.show=false" @addNewBook="addNewBook" @editBook="makeChangesInDb"/>
    </div>
</template>
<script>
import axios from 'axios'
import AddButton from "./components/AddButton.vue";
import MyModal from "./components/MyModal.vue";
import SearchBar from "./components/SearchBar.vue";

export default {
    name: "App",
    components: {
        MyModal,
        AddButton,
        SearchBar
    },
    data() {
        return {
            bookList: [],
            modal: {
                show: false,
                type: ""
            },
            selectedBook: {},
            searching: {
                item: "",
                text: ""
            }
        }
    },
    computed: {
        filtredBooks() {
            if (this.searching.text) {
                if (this.searching.item == "author") {
                    return this.bookList.filter(book => {
                        return book.author.toLowerCase().includes(this.searching.text.toLowerCase())
                    })
                } else {
                    return this.bookList.filter(book => {
                        return book.title.toLowerCase().includes(this.searching.text.toLowerCase())
                    })
                }
            } else {
                return this.bookList
            }
        },
        sortedBooks() {
            const sortBy = function(key, key2) {
                return (a, b) => (a[key] > b[key]) ? 1 : (b[key] > a[key]) ? -1 : (a[key2] > b[key2]) ? 1 : -1
            }
            return [...this.filtredBooks].sort(sortBy('author', 'title'))
        }
    },
    mounted() {
        this.getBookList()
    },
    methods: {
        getBookList() {
            axios
                .get("https://brojor.pythonanywhere.com/books")
                .then((response) => {
                    this.bookList = this.filteredList = response.data
                });
        },
        addNewBook(book) {
            axios({
                    method: 'post',
                    headers: { 'Content-Type': 'application/json' },
                    url: "https://brojor.pythonanywhere.com/books",
                    data: JSON.stringify(book)
                })
                .then(response => {
                    console.log(response)
                    this.author = ""
                    this.title = ""
                    this.getBookList()
                })
        },
        makeChangesInDb(book) {
            console.log(book);
            axios({
                    method: 'put',
                    headers: { 'Content-Type': 'application/json' },
                    url: "https://brojor.pythonanywhere.com/books/" + book.id,
                    data: JSON.stringify(book)
                })
                .then(response => {
                    // console.log()
                    this.selectedBook = response.data
                    this.getBookList()
                })
        },

        selectBook(book) {
            console.log(book.id);
            if (!('id' in this.selectedBook)) {
                console.log("kliknuto, id bylo prázdné");
                this.selectedBook = book
            } else {
                if (this.selectedBook.id != book.id) {
                    console.log("kliknuto, id NEEbylo prázdné");
                    this.selectedBook = {}
                }
            }
        },
        removeBook() {
            this.bookList = this.bookList.filter(book => book.id !== this.selectedBook.id)
            axios({
                    method: 'delete',
                    url: "https://brojor.pythonanywhere.com/books/" + this.selectedBook.id,
                })
                .then(response => {
                    console.log(response)
                })
        },
        switchReaded() {
            const selectedId = this.selectedBook.id
            const book = this.bookList.filter(book => book.id == selectedId)[0]
            const readed = book.readed
            axios({
                    method: 'put',
                    headers: { 'Content-Type': 'application/json' },
                    url: "https://brojor.pythonanywhere.com/books/" + selectedId,
                    data: JSON.stringify({ readed: !readed })
                })
                .then(book.readed = !readed)
        },
        editBook() {
            console.log("edituji knihu");
            this.modal.show = true;
            this.modal.type = "editBook"
        },
        modalAddBook() {
            this.modal.show = true;
            this.modal.type = "addBook"
        }
    },
};
</script>
