<template>
    <div class="modal-bg" @click="clickedOutside">
        <div class="modal">
            <div class="modal-header">
                <h2>{{ headerTitle }}</h2>
                <button @click="$emit('closeModal')" class="close-button">
                    <i class="material-icons">close</i>
                </button>
            </div>
            <div class="modal-body">
                <label for="author">Autor:</label>
                <input v-model="author" type="text" name="author" autocomplete="off">
                <label for="title">Název knihy:</label>
                <input v-model="title" type="text" name="title" autocomplete="off">
                <button @click="submitClicked()" class="submit-button" type="submit">{{ buttonTitle }}</button>
            </div>
        </div>
    </div>
</template>
<script>
export default {

    name: 'MyModal',

    data() {
        return {
            author: "",
            title: "",
            headerTitle: "",
            buttonTitle: "",
        };
    },
    created: function() {
        console.log("ted jsem byl vytvořen, tvůj modal");
        if (this.modalType === "addBook") {
            console.log("jsme v modu addBook");
            this.headerTitle = "Přidání knihy do databáze"
            this.buttonTitle = "Přidat knihu"
            this.author = ""
            this.title = ""
        } else if (this.modalType === "editBook") {
            console.log("jsme v modu editBook");
            this.headerTitle = "Upravit existující knihu"
            this.buttonTitle = "Uložit změny"
            this.author = this.activeBook.author
            this.title = this.activeBook.title
        }
    },
    methods: {
        submitClicked() {
            if (this.author && this.title) {
                let book = { "author": this.author.trim(), "title": this.title.trim() }

                if (this.modalType === "addBook") {
                    this.$emit('addNewBook', book)
                } else if (this.modalType === "editBook") {
                    book.id = this.activeBook.id
                    this.$emit('editBook', book)
                }
                this.$emit('closeModal')
            }
        },
        clickedOutside(event) {
            if (event.target.className == "modal-bg") {
                this.$emit('closeModal')
            }
        }
    },
    props: ['modalType', 'activeBook'],
};
</script>
