<template>
    <form @submit.prevent>
        <button class="switch" @click="SwitchTarget">
            <i v-if="searchTarget == 'title'" class="material-icons">menu_book</i>
            <i v-else class="material-icons">person</i>
            <!-- <i v-else class="material-icons">perm_identity</i> -->
        </button>
        <input v-model="searchText" type="text" @keydown.enter.prevent="search">
        <button @click="search" type="submit" class="search">
            <i class="material-icons">search</i>
        </button>
    </form>
</template>
<script>
export default {

    name: 'SearchBar',

    data() {
        return {
            searchText: "",
            searchTarget: "author"
        };
    },
    methods: {
        search() {
            this.$emit('searching', {item: this.searchTarget, text: this.searchText})
            this.searchText = ""
        },
        SwitchTarget() {
            this.searchTarget = this.searchTarget == "author" ? "title" : "author"
        }
    }
};
</script>
